import React from 'react';
import {assetToSrc, parseAsset} from "../utils";
import { Link } from "react-router-dom";

const WithLink = ({ link, details, children }) => (
    details ?
          <Link to={link}>{children}</Link>
        : children
);

const ProjectTile = ({project, metaImgData}) => {
    const {_id, details, title, description, image} = project;
    const asset = parseAsset(image);
    const imgSrc = assetToSrc(asset, metaImgData);
    const shortDesc = description? description.slice(0, 150) + '... ' : '';
    const tagList = project.tags? project.tags.filter((tag) => (tag.length)).map((tag, idx) => (<small key={idx}>{tag}</small>)): [];

    return (
        <div className={`col-md-6 col-lg-4 item ${details? `zoom-on-hover` : ``}`}>
            <WithLink link={`/project/${_id}`} details={details} >
                <img alt="project img" className="img-fluid image" src={imgSrc} />
                <span className="description">
                    <hr />
                    <span className="description-heading">{ title }</span>
                    <span className="description-body"><div dangerouslySetInnerHTML={{__html: shortDesc}} /></span>
                    <span className="description-tags">{ tagList }</span>
                </span>
            </WithLink>
        </div>);
};

export default ProjectTile;
