import React from 'react';
import { assetToSrc, genMetaImage, parseAsset } from "../utils";
import $ from "jquery";

import { withRouter } from "react-router";
import Helmet from "react-helmet/es/Helmet";
import OtherProjectTile from "../components/OtherProjectTile";

const sanityUrl = "https://7s6fo221.api.sanity.io/v1/data/query/production";
const queryDetail = (id) =>  ("?query=*%5B_type%20%3D%3D%20%22project_summary%22%20%26%26%20_id%20%3D%3D%20%22" + id + "%22%5D");

class InjectSProjectData extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            loading: true,
            projectTitle: 'Project',
            projectDesc: '',
            projectContractor: '',
            project: {}
        };
    }

    getProjectId() {
        return this.props.match.params.id;
    }

    filterOtherProjects(projects) {
        if(this.state.loading) {
            return [];
        } else {
            return projects
                    .filter(({project}) => (project._id !== this.getProjectId() && project.details))
                    .sort(() => (Math.random() - Math.random()))
                    .slice(0, 4);
        }
    }

    componentDidMount() {
        this.fetchProjectData();
    }

    componentDidUpdate(prevProps, prevState) {
        if(prevState.projectId !== this.getProjectId()) {
            this.fetchProjectData();
        }

        if(prevState.loading !== this.state.loading) {
            const $sideImgEl = $("img.project-side-img");
            const $imageDiv = $(".project-main-img .image");

            $sideImgEl.hover((e) => {
                const src = $(e.target).attr('src');

                $imageDiv.fadeTo('fast', 0.1, () => {
                    $imageDiv.css({"background-image": "url(" + src + ")"});
                }).fadeTo('slow', 1);
            }, () => {
                $imageDiv.css({"background-image": "url(" + this.state.mainImg + ")"});
            })
        }
    }

    fetchProjectData() {
        const projectId = this.getProjectId();
        this.setState({projectId: projectId, loading: true});
        window.scrollTo(0, 0);

         return fetch(sanityUrl + queryDetail(projectId))
            .then(resp => resp.json())
            .then(data => {
                if (typeof data !== 'undefined' && data.result && data.result.length) {
                    let projectData = data.result[0];

                    const sideGallery = this.getSlideShow(projectData);
                    const mainImg = this.getMainImg(projectData);

                    this.setState({
                        loading: false,
                        project: projectData,
                        sideGallery,
                        mainImg
                    });
                }
            })
            .catch(function(error) {
                // If there is any error you will catch them here
                console.log(error);
            });
    };

    getSlideShow({slideshow}) {
        let galleryMetaImage = genMetaImage("projectDetail");

        return slideshow? slideshow
            .slice(0, 3)
            .map((sideImg) => {
            return assetToSrc(parseAsset(sideImg), galleryMetaImage);
        }) : [];
    }

    getMainImg({image}) {
        let galleryMetaImage = genMetaImage("projectDetail");
        return assetToSrc(parseAsset(image), galleryMetaImage);
    }

    render() {
        const otherProjects =  this.filterOtherProjects(this.props.projectsData);
        const otherProjectTiles = otherProjects.map(({project, galleryMetaImage}) => <OtherProjectTile key={project._id} project={project} metaImgData={galleryMetaImage}/>);
        const childrenWithProps = React.Children.map(this.props.children, child =>
            React.cloneElement(child, { ...this.state.project, otherProjectTiles, sideGallery: this.state.sideGallery, mainImg: this.state.mainImg})
        );

        return (
            <div>
                <Helmet>
                    <title>{ this.state.projectTitle }</title>
                    <meta name="description" content={ this.state.projectDesc }  />
                </Helmet>
                {childrenWithProps}
            </div>
        );
    }
}

export default withRouter(InjectSProjectData);