import React from 'react';
import TypedLib from 'typed.js';

class Typed extends React.Component {
    componentDidMount() {
        const { strings } = this.props;

        const options = {
            strings: strings.map((fact) => {
                return 'guest$ echo rand fact -type lemur ^1000\n `</br>' + fact + '</br> guest$ `';
            }, ''),
            smartBackspace: false,
            typeSpeed: 40,
            preStringTyped: (arrayPos, self) => {
                let currString = self.strings[arrayPos];
                self.backDelay = 40 * currString.length;
            },
            loop: true,
            loopCount: Infinity,
        };

        this.typed = new TypedLib(this.el, options);
    }

    componentWillUnmount() {
        this.typed.destroy();
    }

    render() {
        return (
          <span ref={(el) => { this.el = el; }}/>
        );
    }
}

export default Typed;