import React from 'react';
import { genMetaImage } from "../utils";
import ProjectTile from "../components/ProjectTile";

const sanityUrl = "https://7s6fo221.api.sanity.io/v1/data/query/production";
const query = "?query=*%5B_type%20%3D%3D%20%22project_summary%22%5D";

class InjectProjectData extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            projects: []
        }
    }

    componentDidMount() {
        this.fetchProjectData();
    }

    fetchProjectData() {
        return fetch(sanityUrl + query)
            .then(resp => resp.json())
            .then(data =>{
                return data? data.result.sort((a,b) => (a.order - b.order)) : null;

            })
            .then( projectSummaries => {
                let galleryMetaImage = genMetaImage("projects");

                const projects = projectSummaries.map((project) => ({project, galleryMetaImage}));
                this.setState({projects});
            })
            .catch(function(error) {
                // If there is any error you will catch them here
            });
    }

    render() {
        const projectTiles = this.state.projects.map(({project, galleryMetaImage}) => <ProjectTile key={project._id} project={project} metaImgData={galleryMetaImage}/>);
        const childrenWithProps = React.Children.map(this.props.children, child =>
            React.cloneElement(child, { projects: projectTiles, projectsData: this.state.projects})
        );

        return <div>{childrenWithProps}</div>;
    }
}

export default InjectProjectData;