import React from 'react';
import {assetToSrc, parseAsset} from "../utils";
import { Link } from "react-router-dom";

const OtherProjectTile = ({project, metaImgData}) => {
    const {_id, image} = project;
    let asset = parseAsset(image);
    let imgSrc = assetToSrc(asset, metaImgData);

    return (
        <div className="col-md-4 col-lg-3">
            <div className="item">
                <Link to={`/project/${_id}`}>
                    <img alt="other-project" className="img-fluid scale-on-hover" src={imgSrc} />
                </Link>
            </div>
        </div>);
};

export default OtherProjectTile;
